<template>
  <div class="suggest-history-layout">
    <header class="feedback-history-header">
      <a @click="goPrePage()" class="feedback-cancle"></a>
      <h2 class="feedback-title">反馈记录</h2>
    </header>
    <main class="history-main-layout clearfix" v-show="!isNoData">
      <ul>
        <li class="history-item" v-for="item in historyDatas">
          <div class="mine-history-info">
            <span class="mine-history-time">
              反馈时间：{{item.suggest_time}}
            </span>
            <p class="mine-history-text">
              {{item.suggest_content}}
            </p>
          </div>
        </li>
      </ul>
    </main>
    <NoData v-show="isNoData"></NoData>
  </div>
</template>
<script>
  import Api from '../../../api/api';
  import {suggestHistoryInfo} from '../../../api/suggestfeed';
  import NoData from '../../nodata/NoData.vue';
  export default{
      components:{
        NoData
      },
      data(){
        return{
            historyDatas:[],
            isNoData:false,
        }
      },
      mounted(){
        this.$nextTick(function () {
          this.getSuggestDatas();
        })
      },
      methods:{
        getSuggestDatas(){
          const _this = this;
          suggestHistoryInfo(Api.SUGGESTBACK).then((res)=>{
              if(res.status === 200 &&res.data.data!==null){
                _this.historyDatas = res.data.data.detail;
                const len = _this.historyDatas.length;
                len>1 ? _this.isNoData = false : _this.isNoData = true;
              }else{
                _this.dealErr();
              }
          },(err)=>{
                _this.dealErr();
          });
        },
        dealErr(){
          this.showMsg('获取反馈记录失败，请重试!');
          this.$store.dispatch('hideLoading');
          this.isNoData = true;
        },
        showMsg(str){
          this.$toast({
            message: str,
            duration: 2000,
          });
        },
        goPrePage(){
          window.history.go(-1);
        },
      }
  }
</script>
<style scoped>
  @import "../../../assets/css/pcsuggestht.css";
</style>



